// const empty = { 'metadata': {}, 'spec': {}, 'status': {} }

export function queryBuild (buildList, name, version) {
  const index = buildList.items.findIndex(i => {
    return i.metadata.name === name + '-' + version
  })
  if (index === -1) {
    return undefined
  }
  return buildList.items[index]
}

export function queryDeployConfig (configList, name) {
  const index = configList.items.findIndex(i => {
    return i.metadata.name === name
  })
  if (index === -1) return undefined

  return configList.items[index]
}

export function queryController (list, name, version) {
  const index = list.items.findIndex(i => {
    return i.metadata.name === name + '-' + version
  })
  if (index === -1) {
    return undefined
  }
  return list.items[index]
}

export function queryPod (list, name, version) {
  const index = list.items.findIndex(i => {
    // eslint-disable-next-line eqeqeq
    return (i.metadata.name.includes(name) && i.metadata.annotations['openshift.io/deployment-config.latest-version'] == version)
  })
  if (index === -1) {
    return undefined
  }
  return list.items[index]
}

export function queryPodForBuild (list, name) {
  const index = list.items.findIndex(i => {
    return (i.metadata.name.includes(name) && i.metadata.name.includes('-build'))
  })
  if (index === -1) {
    return undefined
  }
  return list.items[index]
}
