<template>
  <b-container fluid>
    <b-tabs>
      <!-- DEPLOY TAB -->
      <b-tab title="Deployment" active>
        <b-row>
          <b-col cols="6">
            <basic-table
              :table-data="deployHistoryData"
              :fields="deployHistoryFields"
              :hover="true"
              :bordered="true"
              @row-clicked="deployTbSelected"
            />
          </b-col>
          <b-col cols="6">
            <b-tabs v-model="deployLogTab">
              <b-tab title="Details" active>
                <simple-grid :dataset="deployDetail"/>
              </b-tab>
              <b-tab title="Logs">
                <log-area :message="logMessage"></log-area>
              </b-tab>
            </b-tabs>
          </b-col>
        </b-row>
      </b-tab>
      <!-- BUILD TAB -->
      <b-tab title="Build">
        <b-row>
          <div class="col text-right">
            <b-button @click="startBuild">Start Build</b-button>
          </div>
        </b-row>
        <b-row>
          <b-col cols="6">
            <basic-table
              :table-data="buildHistoryData"
              :fields="buildHistoryFields"
              :hover="true"
              :bordered="true"
              @row-clicked="buildTbSelected"
            />
          </b-col>
          <b-col cols="6">
            <b-tabs v-model="buildLogTab">
              <b-tab title="Details" active>
                <simple-grid :dataset="buildDetail"/>
              </b-tab>
              <b-tab title="Logs">
                <log-area :message="logMessage"></log-area>
              </b-tab>
            </b-tabs>
          </b-col>
        </b-row>
      </b-tab>
    </b-tabs>
    <one-button-alert name="defailError" ref="defailError" />
  </b-container>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import OneButtonAlert from '../components/modal/OneButtonAlert.vue'
import BasicTable from '../components/table/Table.vue'
import SimpleGrid from '../components/grid/SimpleGrid.vue'
import LogArea from '../components/textarea/LogArea.vue'
// eslint-disable-next-line no-unused-vars
import { queryBuild, queryDeployConfig, queryController, queryPod, queryPodForBuild } from '../utils/Query'
import { timeSince } from '../utils/Date'

export default {
  name: 'detail',
  components: {
    BasicTable,
    SimpleGrid,
    LogArea,
    OneButtonAlert
  },
  props: {
    projectName: {
      type: String,
      default: () => this.currentProject
    },
    packageName: {
      type: String,
      default: () => this.currentPackage
    },
    lastBuild: {
      type: String,
      default: () => '#1'
    },
    lastDeploy: {
      type: String,
      default: () => '#1'
    }
  },
  data () {
    return {
      deployHistoryFields: [
        { key: 'version', label: 'Last Version' },
        { key: 'history', label: 'Status' },
        { key: 'age', label: 'Created' }
      ],
      buildHistoryFields: [
        { key: 'version', label: 'Last Build' },
        { key: 'history', label: 'Status' },
        { key: 'age', label: 'Created' }
      ],
      selectedDeployVersion: this.lastDeploy.slice(1),
      selectedBuildVersion: this.lastBuild.slice(1),
      logMessage: '',
      deployLogTab: 0,
      buildLogTab: 0
    }
  },
  computed: {
    ...mapState('project', ['currentProject', 'currentPackage']),
    ...mapState('build', ['buildConfigs', 'buildList']),
    ...mapState('deploy', ['deployConfigs']),
    ...mapState('pod', ['podList']),
    ...mapState('controller', ['controllerList']),
    deployHistoryData () {
      let dataset = []
      this.controllerList.items.forEach(i => {
        if (i.metadata.labels.app === this.packageName) {
          dataset.push({
            version: '#' + i.metadata.annotations['openshift.io/deployment-config.latest-version'],
            history: i.metadata.annotations['openshift.io/deployment.phase'],
            age: timeSince(i.metadata.creationTimestamp)
          })
        }
      })
      dataset.sort(this.sortFunction)
      if (dataset[0]) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.selectedDeployVersion = dataset[0].version.slice(1)
      }
      return dataset
    },
    buildHistoryData () {
      let dataset = []
      this.buildList.items.forEach(i => {
        if (i.metadata.annotations['openshift.io/build-config.name'] === this.packageName) {
          dataset.push({
            version: '#' + i.metadata.annotations['openshift.io/build.number'],
            history: i.status.phase,
            age: timeSince(i.metadata.creationTimestamp)
          })
        }
      })
      dataset.sort(this.sortFunction)
      if (dataset[0]) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.selectedBuildVersion = dataset[0].version.slice(1)
      }
      return dataset
    },
    deployDetail () {
      let detail = []
      // NOTE, Status
      let status = [
        { label: 'Status', data: '-' },
        { label: 'IP', data: '-' },
        { label: 'Node', data: '-' },
        { label: 'Restart Policy', data: '-' }
      ]
      let container = [
        { label: 'Status', data: '-' },
        { label: 'Ready', data: '-' },
        { label: 'RestartCount', data: '-' }
      ]
      let template = [
        { label: 'Image', data: '-' },
        { label: 'Build', data: '-' },
        { label: 'Source', data: '-' },
        { label: 'Ports', data: '-' },
        { label: 'Mount', data: '-' }
      ]
      let volumes = [
        { label: 'Type', data: '-' },
        { label: 'Secret', data: '-' }
      ]
      let controller = queryController(this.controllerList, this.packageName, this.selectedDeployVersion)
      window.controller = controller
      if (controller) {
        status[0].data = controller.metadata.annotations['openshift.io/deployment.phase']
        template[0].data = controller.spec.template.spec.containers[0].image.substring(0,
          controller.spec.template.spec.containers[0].image.indexOf('@sha'))
        if (controller.spec.template.spec.containers[0].ports) {
          template[3].data = controller.spec.template.spec.containers[0].ports[0].containerPort + '/' +
                             controller.spec.template.spec.containers[0].ports[0].protocol
        } else {
          // host network
        }
        template[4].data = controller.spec.template.spec.containers[0].volumeMounts
        volumes[0].data = controller.spec.template.spec.volumes
      }
      let build = queryPodForBuild(this.podList, this.packageName)
      if (build) {
        template[1].data = build.metadata.annotations['openshift.io/build.name']
      }
      let pod = queryPod(this.podList, this.packageName, this.selectedDeployVersion)
      window.pod = pod
      if (pod) {
        status[0].data = pod.status.phase
        status[1].data = pod.status.podIP
        status[2].data = pod.spec.nodeName
        status[3].data = pod.spec.restartPolicy

        if (pod.status.containerStatuses[0].ready === true) {
          container[0].data = pod.status.phase + ' since ' + pod.status.containerStatuses[0].state.running
        } else {
          container[0].data = JSON.stringify(pod.status.containerStatuses[0].state)
        }
        container[1].data = pod.status.containerStatuses[0].ready
        container[2].data = pod.status.containerStatuses[0].restartCount
        template[4].data = pod.spec.containers[0].volumeMounts[0].name + ' -> ' +
          pod.spec.containers[0].volumeMounts[0].mountPath + ' ' +
          (pod.spec.containers[0].volumeMounts[0].readOnly ? 'read-only' : '')
      }
      detail.push({ title: 'Status', data: status })
      detail.push({ title: 'Container', data: container })
      detail.push({ title: 'Template', data: template })
      detail.push({ title: 'Volumes', data: volumes })
      return detail
    },
    buildDetail () {
      let detail = []
      let status = [
        { label: 'Status', data: '-' },
        { label: 'Started', data: '-' },
        { label: 'Duration', data: '-' }
      ]
      let config = [
        { label: 'Build Strategy', data: '-' },
        { label: 'Builder Image', data: '-' },
        { label: 'Source Type', data: '-' },
        { label: 'Source Repo', data: '-' },
        { label: 'Source Ref', data: '-' },
        { label: 'Source Commit', data: '-' },
        { label: 'Output Image', data: '-' }
      ]
      const build = queryBuild(this.buildList, this.packageName, this.selectedBuildVersion)
      if (build) {
        status[0].data = build.status.phase
        status[1].data = timeSince(build.status.startTimestamp)
        status[2].data = build.status.duration
        config[0].data = build.spec.strategy.type
        config[1].data = build.spec.strategy[build.spec.strategy.type.toLowerCase() + 'Strategy'].from.name

        config[2].data = build.spec.source.type
        config[3].data = build.spec.source.git.uri
        config[4].data = build.spec.source.git.ref
        config[5].data = build.spec.revision.git.message + ' authored by ' + build.spec.revision.git.author.email
        config[6].data = build.spec.output.to.name
      }
      detail.push({ title: 'Status', data: status })
      detail.push({ title: 'Configuration', data: config })
      return detail
    },
    getSelectedDeployVersion () {
      return this.selectedDeployVersion
    },
    getSelectedBuildVersion () {
      return this.selectedBuildVersion
    }
  },
  mounted () {
    this.requestLists()
  },
  methods: {
    ...mapActions('build', [
      'getBuildConfigList',
      'getBuildList',
      'getBuildLog',
      'instantBuild',
      'watchBuild']),
    ...mapActions('deploy', ['getDeployConfigList']),
    ...mapActions('pod', ['getPodList', 'getPodLog']),
    ...mapActions('controller', ['getControllerList']),
    requestLists () {
      if (this.buildConfigs.items.length === 0) {
        this.getBuildConfigList(this.projectName).then((list) => {
          this.getBuildList(this.projectName)
        })
        this.getDeployConfigList(this.projectName).then((list) => {
          this.getPodList(this.projectName)
          this.getControllerList(this.projectName)
        })
      }
    },
    deployTbSelected (item, index, event) {
      this.selectedDeployVersion = item.version.slice(1)
      this.getDeployLog()
    },
    buildTbSelected (item, index, event) {
      this.selectedBuildVersion = item.version.slice(1)
      this.getDeployLog()
    },
    getDeployLog () {
      this.logMessage = ''
      let pod = queryPod(this.podList, this.packageName, this.selectedDeployVersion)
      if (pod) {
        this.getPodLog({
          projectName: this.projectName,
          podName: pod.metadata.name
        }).then((log) => {
          this.logMessage = log
        })
      } else {
        this.logMessage = ''
      }
    },
    getRosBuildLog () {
      this.logMessage = ''
      let build = queryBuild(this.buildList, this.packageName, this.selectedBuildVersion)
      if (build) {
        this.getBuildLog({
          projectName: this.projectName,
          buildName: build.metadata.name
        }).then((log) => {
          this.logMessage = log
        })
      }
    },
    startBuild () {
      this.instantBuild({
        projectName: this.projectName,
        buildName: this.packageName
      })
        .then(response => {
          this.watchBuild(this.projectName)
        })
        .catch(error => {
          this.$refs.packageError.show({
            title: this.$i18n.t('popup.startBuild'),
            text: error
          })
        })
    },
    sortFunction (a, b) {
      let aVersion = a.version.slice(1)
      let bVersion = b.version.slice(1)
      return Number(aVersion) > Number(bVersion) ? -1 : Number(aVersion) < Number(bVersion) ? 1 : 0
    }
  },
  watch: {
    deployLogTab (idx) {
      // active log tab
      if (idx === 1) {
        this.getDeployLog()
      }
    },
    buildLogTab (idx) {
      // active log tab
      if (idx === 1) {
        this.getRosBuildLog()
      }
    }
  }
}
</script>

