<template>
   <b-form-textarea
    id="log-area"
    rows="30"
    max-rows="30"
    v-model="currentLog"
  ></b-form-textarea>
</template>

<script>
export default {
  name: '',
  props: {
    message: {
      type: String,
      default: () => ''
    }
  },
  data () {
    return {
    }
  },
  computed: {
    currentLog () {
      return this.message
    }
  }
}
</script>

